.main-nav-toggle {
  position: absolute;
  top: 0;
  right: 0;
  border: none;
  outline: none;
  width: 60px;
  height: 60px;
  background: none;
  cursor: pointer;

  @media (min-width: 320px) {
    top: 10px;
    // right: 10px;
  }

  @media (min-width: 768px) {
    right: 20px;
  }

  @media (min-width: 1024px) {
    display: none;
  }
}

.main-nav {
  // position: relative;
  display: flex;
  justify-content: center;
  align-items: center;
  // flex-direction: column;

  & .btn-anketa {
    @media (max-width: 1023px) {
      display: none;
    }
  }

  &--opened .btn-anketa {
    @media (min-width: 320px) {
      display: none;
    }

    @media (min-width: 768px) {
      display: flex;
      align-self: flex-start;
      margin-top: auto;
      margin-bottom: auto;
      margin-right: 60px;
      // margin: auto;
    }


  }

  &--closed .main-nav-toggle::before {
    content: "";
    position: absolute;
    top: 20px;
    right: 25px;
    width: 24px;
    height: 1.5px;
    background-color: black;
    box-shadow: 0 8px 0 0 black, 0 16px 0 0 black;

    @media (min-width: 1024px) {
      display: none;
    }
  }

  &--closed .main-menu {
    @media (max-width: 1023px) {
      display: none;
    }
  }

  &--opened .main-menu {
    flex-direction: column;
    justify-content: space-around;
    height: 90vh;

    & a {
      font-size: 24px;
      line-height: 26px;
    }
  }

  &--opened .main-nav-toggle {
    position: absolute;
    top: 5;
    right: 0;
    border: none;
    width: 60px;
    height: 60px;
    background: none;
    cursor: pointer;

    &::before,
    &::after {
      content: "";
      position: absolute;
      top: 30px;
      right: 20px;
      width: 25px;
      height: 2px;
      background-color: black;
      cursor: pointer;
    }

    &::before {
      transform: rotate(45deg);
    }

    &::after {
      transform: rotate(-45deg);
    }

    @media (min-width: 1024px) {
      display: none;
    }
  }
}
